<template>
  <div class="container">
    <van-tabs v-model="activeIndex" swipeable :lazy-render="false" @change="changeTab">
      <van-tab v-for="item in channelsArr" :title="item.name" :key="item.id">
        <!-- 滚动容器 -->
        <div class="scroll-wrapper" @scroll="remember($event)" ref="scollContainer">
          <van-pull-refresh
            v-model="activeChannel.isLoading"
            @refresh="onRefresh"
            :success-text="refreshSuccessText"
          >
            <van-list
              v-model="activeChannel.loading"
              :finished="activeChannel.finished"
              finished-text="没有更多了"
              @load="onLoad"
            >
              <van-cell
                :to="{name:'article',params:{id:item.art_id.toString()}}"
                v-for="(item,i) in activeChannel.articleList"
                :key="i"
              >
                <!-- <div style="height:100%;">1</div> -->

                <div class="article_item">
                  <h3 class="van-ellipsis">{{item.title}}</h3>
                  <div class="img_box" v-if="item.cover.type===1">
                    <!-- lazy-load是给van-image用的，v-lazy是给image用的需要下载lazy-laod插件-->
                    <van-image class="w100" fit="cover" :src="item.cover.images[0]" lazy-load />
                  </div>
                  <div class="img_box" v-if="item.cover.type===3">
                    <van-image
                      v-for="(item,i) in item.cover.images"
                      :key="i"
                      class="w33"
                      fit="cover"
                      :src="item"
                      lazy-load
                    />
                  </div>
                  <div class="info_box">
                    <span>{{item.aut_name}}</span>
                    <span>{{item.comm_count}}</span>
                    <span>{{item.pubdate|relTime}}</span>
                    <span
                      class="close"
                      v-if="user.token"
                      @click.stop="openMoreAction(item.art_id.toString())"
                    >
                      <van-icon name="cross"></van-icon>
                    </span>
                  </div>
                </div>
              </van-cell>
            </van-list>
          </van-pull-refresh>
        </div>
        <!-- <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
          <van-cell v-for="item in list" :key="item" :title="item" />
        </van-list>-->
      </van-tab>
    </van-tabs>
    <!-- 频道按钮 -->
    <span slot="nav-right" class="bar_btn" @click="openChannelEdit">
      <van-icon name="wap-nav" />
    </span>
    <!--父组件 v-model相当于v-bind:value='show'和
    @input='this.show=$event.target.value'
    而在子组件（component中的MoreAction.vue）中正好用的是v-model属性
    （因为是v-model双向绑定所以拆开来了）
     <van-popup :value="value" @input="$emit('input',$event)">，
       相当于父子组件传值一一对应了
       1.其中子组件用props接收父组件v-bind绑定的value，
       2.子组件中又$emit('input',$event),父组件的v-model相当于@input='this.show=$event.target.value'即监听了子组件定义的input事件
       3.子组件中之所以写value和input是因为vant定义了子组件使用v-model来控制显示和隐藏
    -->
    <more-action
      v-if="user.token"
      v-model="show"
      :articleId="articleId"
      @on-dislikes="removeArticle()"
      @on-report="removeArticle()"
      @on-delete="changeTab"
    ></more-action>
    <!--
       :activeIndex.sync="activeIndex"是
     （ :activeIndex="activeIndex"
    @update="activeIndex=$event"）的语法糖，
    父子组件传值后不用再定义个方法把子组件值传出来直接用.sync就可以双向绑定，
    和v-model类似（子组件在components/channel-edit.vue）-->
    <channel-edit
      v-model="showChannelEdit"
      :channelsArr="channelsArr"
      :activeIndex.sync="activeIndex"
      @on-delete="getChannels"
    ></channel-edit>
  </div>
</template>

<script>
import { getChannels } from '@/api/channels'
import { getArticles } from '@/api/articles'
import { mapState } from 'vuex'
import MoreAction from './components/MoreAction'
import ChannelEdit from './components/channel-edit'
export default {
  name: 'home-index',
  components: { MoreAction, ChannelEdit },
  data () {
    return {
      // 频道数组
      channelsArr: [],
      // 当前tab栏选中的频道的id(默认为第一个)
      activeIndex: 0,
      // 下拉刷新提示文字
      refreshSuccessText: '',
      // // 加载传入的时间戳
      // timestamp: Date.now(),
      // articleList: [],
      // // v-model是否处于加载状态，加载过程中不触发load事件
      // loading: false,
      // // finished是否已加载完成，加载完成后不再触发load事件
      // finished: false,
      // // 下拉刷新的数据
      // count: 0,
      // isLoading: false
      // 举报文章弹出框显示隐藏
      show: false,
      // 不感兴趣时传入子组件的文章id
      articleId: null,
      // 频道列表弹出框显示隐藏
      showChannelEdit: false
    }
  },
  computed: {
    // 将频道数组中当前频道对应的数据写成计算属性动态取出来，
    // 一旦切换了tab对应数据也要跟着变，所以用计算属性监听
    activeChannel () {
      return this.channelsArr[this.activeIndex]
    },
    // mapState数据必须写在computed里面的
    ...mapState(['user'])
  },
  // 监听登录与退出
  // 不管登录与退出，都需要操作 vuex 中用户数据
  // 监听用户数据改变即可
  watch: {
    user () {
      this.activeIndex = 0
      this.getChannels()
      this.onLoad()
    }
  },
  methods: {
    // 打开频道弹出框
    openChannelEdit () {
      this.showChannelEdit = true
    },
    // 打开对话框
    openMoreAction (id) {
      this.show = true
      this.articleId = id
    },
    // 删除文章
    removeArticle () {
      const index = this.activeChannel.articleList.findIndex(item => {
        return item.art_id.toString() === this.articleId
      })
      this.activeChannel.articleList.splice(index, 1)
    },
    // load滚动条与底部距离小于 offset 时触发(就是下拉时触发)
    // @load特点：默认在组件初始化会加载一次。
    // @load特点：当加载的内容渲染后不足一屏，继续触发一次。
    // @load事件触发条件：往上滑动到底部触发。
    async onLoad () {
      // 异步更新数据
      // 访问接口取数据
      // console.log(this.activeIndex)
      // console.log('onload' + this.activeChannel.timestamp)
      // console.log(this.activeChannel.loading)
      // console.log(
      //   this.activeChannel.timestamp + '----------' + this.activeChannel.id
      // )
      await this.$sleep()
      const data = await getArticles({
        channelId: this.activeChannel.id,
        timestamp: this.activeChannel.timestamp
      })
      // console.log(data)
      this.activeChannel.loading = false
      this.activeChannel.articleList.push(...data.results)
      // 加载状态结束
      // console.log(this.activeChannel.loading)
      // 数据全部加载完成
      if (!data.pre_timestamp) {
        this.activeChannel.finished = true
      } else {
        this.activeChannel.timestamp = data.pre_timestamp
      }
    },
    async onRefresh () {
      this.activeChannel.timestamp = Date.now()
      const data = await getArticles({
        channelId: this.activeChannel.id,
        timestamp: this.activeChannel.timestamp
      })
      this.activeChannel.isLoading = false
      // console.log(data)
      if (data.results.length) {
        this.activeChannel.articleList = data.results
        this.refreshSuccessText = '刷新成功'

        // 更新当前数据的时间戳
        this.activeChannel.timestamp = data.pre_timestamp

        // 有可能不满足一屏，主动加载一次数据
        // (后台可能会有算法给你的不是从前往后的10条，而是刚更新的多少条，或者按照你的喜好给的几条)
        this.onLoad()
      } else {
        this.refreshSuccessText = '刷新失败'
      }
    },
    // 获取所有频道
    async getChannels () {
      // console.log('-----' + this.user.token)

      const data = await getChannels()
      console.log(data)
      // 清空频道列表避免登录后频道列表顺序乱了（）
      this.channelsArr = []
      // this.channelsArr = data.channels
      // map() 方法返回一个新数组，数组中的元素为原始数组元素调用函数处理后的值。
      // map()方法按照原始数组元素顺序依次处理元素。
      // 注意:map() 不会对空数组进行检测。
      // 注意：map() 不会改变原始数组。

      // 一进页面就将各个频道的下拉刷新和上拉加载的初始化数据放进频道数组里，
      // 同时将页面获取文章列表的参数timestamp初始化，将页面所需的文章列表双向绑定
      // 后面直接操作频道对应的每一条就可以了

      // this.$nextTick(() => {
      this.channelsArr = data.channels.map(item => {
        return {
          id: item.id,
          name: item.name,
          // 获取文章列表传入的时间戳
          timestamp: Date.now(),
          articleList: [],
          // v-model是否处于加载状态，加载过程中不触发load事件
          loading: false,
          // finished是否已加载完成，加载完成后不再触发load事件
          finished: false,
          // 下拉刷新的数据
          isLoading: false,
          scrollTop: 0
        }
      })
      // })
    },
    // tab组件，默认是懒加载，会导致操作dom问题，需要关闭，自己主动来加载。
    // 绑定滚动事件，记录阅读位置，使用scrollTop 激活组件的时候，操作当前列表，滚动到阅读位置
    // 切换tab组件的时候
    // 如果之前没有加载过数据，开启上拉加载效果，调用加载函数。
    // 如果有数据，操作当前列表，滚动到阅读位置 (默认tab组件滚动到顶部)
    // 切换组件时（例如点击搜索再返回时），vant的tab标签会默认将位置回到最顶部，所以需要处理一下

    // 切换tab组件的时候
    changeTab () {
      if (!this.activeChannel.articleList.length) {
        // 如果之前没有加载过数据，开启上拉加载效果，调用加载函数
        this.activeChannel.loading = true
        this.onLoad()
      } else {
        // tab默认回到顶部的操作是在手动操作滚动到阅览位置之后的，
        // 所以需要用$nextTick将执行顺序改为手动操作滚动到阅览位置最后执行
        this.$nextTick(() => {
          const dom = this.$refs['scollContainer'][this.activeIndex]
          dom.scrollTop = this.activeChannel.scrollTop
        })
      }
    },
    // 监听滚动事件，记录滚动位置
    remember (event) {
      this.activeChannel.scrollTop = event.target.scrollTop
      // console.log(this.activeChannel.scrollTop)
    }
  },
  // 当激活组件时需要滚动到的阅览位置
  activated () {
    if (this.$refs['scollContainer']) {
      const dom = this.$refs['scollContainer'][this.activeIndex]
      dom.scrollTop = this.activeChannel.scrollTop
      // console.log(dom.scrollTop)
    }
  },
  created () {
    // this.onLoad()
    this.getChannels()
  }
}
</script>

<style lang="less" scoped>
.van-tabs {
  height: 100%;
  display: flex;
  flex-direction: column;
  margin-bottom: 35px;
  /deep/ .van-tabs__wrap {
    height: 36px;
    padding-right: 36px;
    .van-tab {
      line-height: 36px;
    }
    .van-tabs__line {
      background-color: #3296fa;
      height: 2px;
    }
  }
  /deep/ .van-tabs__content {
    flex: 1;
    overflow: hidden;
  }
  /deep/ .van-tab__pane {
    height: 100%;
    .scroll-wrapper {
      height: 100%;
      overflow-y: auto;
    }
  }
}
.bar_btn {
  width: 36px;
  height: 35px;
  position: absolute;
  top: 0;
  right: 0;
  &::before {
    content: '';
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 999;
    box-shadow: 0 0 10px #999;
    transform: scale(1, 0.6);
  }
  .van-icon-wap-nav {
    width: 100%;
    height: 100%;
    background: #fff;
    text-align: center;
    line-height: 35px;
    position: relative;
    z-index: 1000;
    &::before {
      font-size: 20px;
    }
  }
}
.article_item {
  h3 {
    font-weight: normal;
    line-height: 2;
  }
  .img_box {
    display: flex;
    justify-content: space-between;
    .w33 {
      width: 33%;
      height: 90px;
    }
    .w100 {
      width: 100%;
      height: 180px;
    }
  }
  .info_box {
    color: #999;
    line-height: 2;
    position: relative;
    font-size: 12px;
    span {
      padding-right: 10px;
      &.close {
        border: 1px solid #ddd;
        border-radius: 2px;
        line-height: 15px;
        height: 12px;
        width: 16px;
        text-align: center;
        padding-right: 0;
        font-size: 8px;
        position: absolute;
        right: 0;
        top: 7px;
      }
    }
  }
}
</style>
