<template>
  <van-action-sheet
    title="编辑频道"
    :value="value"
    @input="$emit('input',$event)"
    @closed="editing=false"
  >
    <div class="channel">
      <div class="tit">
        我的频道：
        <span class="tip">点击可进入频道</span>
        <van-button v-if="!editing" @click="editing=true" size="mini" type="info" plain>编辑</van-button>
        <van-button v-else @click="editing=false" size="mini" type="danger" plain>完成</van-button>
      </div>
      <van-grid class="van-hairline--left">
        <van-grid-item v-for="(item,i) in channelsArr" :key="item.id">
          <span class="f12" :class="{red:activeIndex===i}" @click="enterChannel(i)">{{item.name}}</span>
          <van-icon v-show="editing&&i!=0" class="btn" name="cross" @click="delChannel(i,item.id)"></van-icon>
        </van-grid-item>
      </van-grid>
    </div>
    <div class="channel">
      <div class="tit">可选频道：</div>
      <van-grid class="van-hairline--left">
        <van-grid-item v-for="item in optionalChannels" :key="item.id">
          <span class="f12">{{item.name}}</span>
          <van-icon class="btn" name="plus" @click="addChannel(item)"></van-icon>
        </van-grid-item>
      </van-grid>
    </div>
  </van-action-sheet>
</template>

<script>
import { getAllChannels, delChannel, addChannel } from '@/api/channels'
export default {
  props: {
    value: {
      type: Boolean,
      default: false
    },
    channelsArr: {
      type: Array,
      // 如果默认值是复杂数据类型，必须是函数返回
      default: () => []
    },
    // 激活的频道索引
    activeIndex: {
      type: Number,
      default: 0
    }
  },
  data () {
    return {
      editing: false,
      // 全部频道
      allChannels: []
    }
  },
  methods: {
    // 点击添加频道
    async addChannel ({ id, name }) {
      try {
        // 生成接口所需要的带seq（序号）的数据
        const orderChannels = this.channelsArr.map((item, i) => {
          return {
            id: item.id,
            name: item.name,
            seq: i
          }
        })
        // 追加频道
        orderChannels.push({ id, name, seq: orderChannels.length })
        // 后台不用推荐
        orderChannels.splice(0, 1)
        // 调用添加接口
        await addChannel(orderChannels)
        // 提示
        this.$toast.success('添加成功')
        //
        this.channelsArr.push({
          id: id,
          name: name,
          // 获取文章列表传入的时间戳
          timestamp: Date.now(),
          articleList: [],
          // v-model是否处于加载状态，加载过程中不触发load事件
          loading: false,
          // finished是否已加载完成，加载完成后不再触发load事件
          finished: false,
          // 下拉刷新的数据
          isLoading: false,
          scrollTop: 0
        })
      } catch (error) {
        this.$toast.fail('添加失败')
      }
    },
    //   获取全部频道
    async getAllChannels () {
      const data = await getAllChannels()
      this.allChannels = data.channels
    },
    // 点击可进入频道
    enterChannel (i) {
      // 把当前的index传递给父组件
      // 父组件去修改 activeIndex的值为index即可

      // 父组件为Home.vue-----------------------
      // this.$emit('update:自定义属性') 触发 自定义属性.sync 绑定的属性
      // 多个值父子组件双向绑定时写多个this.$emit('update:自定义属性')
      this.$emit('update:activeIndex', i)
      //   关闭对话框
      this.$emit('input', false)
    },
    // 删除关注的频道
    async delChannel (index, channelId) {
      try {
        await delChannel(channelId)
        // 2. 提示
        this.$toast.success('删除成功')
        // 3. （删除的频道索引小于或等于当前激活的频道索引）当前激活的频道前移一位
        if (index <= this.activeIndex) {
          // 修改父组件的数据
          this.$emit('update:activeIndex', this.activeIndex - 1)
        }
        console.log(this.activeIndex)

        // 4. 删除myChannels数据中index对应频道
        // 4.1 父传子  使用props来接收  数据特点：只读（单向数据流）
        // 4.2 当你去修改props中的数据，在不改变其引用这个条件下，是可以修改数据。
        // 总结：传入的是数据或对象，可以修改值（父组件受影响），但是不能重新赋值。
        this.channelsArr.splice(index, 1)
        // 5. 通知父组件去调用changeChannel函数(如果之前访问过对应channel页面自然是有数据的，
        //  但是如果之前没有访问过，是需要手动调用加载函数来加载数据的)
        this.$emit('on-delete')
      } catch (error) {
        this.$toast.fail('删除失败')
      }
    }
  },
  computed: {
    //   全部频道减去用户已关注频道就是可选频道
    optionalChannels () {
      return this.allChannels.filter(
        item =>
          this.channelsArr.findIndex(myitem => myitem.id === item.id) === -1
      )
    }
  },
  created () {
    this.getAllChannels()
  }
}
</script>

<style lang="less" scoped>
.van-popup--bottom {
  &.van-popup--round {
    border-radius: 0;
  }
}
.van-action-sheet {
  max-height: 100%;
  height: 100%;
  .van-action-sheet__header {
    background: #3296fa;
    color: #fff;
    .van-icon-close {
      color: #fff;
    }
  }
}
.channel {
  padding: 10px;
  .tit {
    line-height: 3;
    .tip {
      font-size: 10px;
      color: #999;
    }
  }
  .van-button {
    float: right;
    margin-top: 7px;
  }
  .btn {
    position: absolute;
    bottom: 0;
    right: 0;
    background: #ddd;
    font-size: 12px;
    color: #fff;
  }
  .f12 {
    font-size: 12px;
    color: #555;
  }
  .red {
    color: red;
  }
}
</style>
