<template>
  <!-- 弹出框  默认隐藏 -->
  <!-- 此处本来是vant定义的v-model来控制显示和隐藏的，
  但是因为子组件中的props只能获取不能改变值，而v-model是双向绑定值的变化的，所以拆开来写了-->
  <van-popup :value="value" @input="$emit('input',$event)" @open="isReport=false">
    <van-cell-group v-if="!isReport">
      <van-cell @click="dislikes">不感兴趣</van-cell>
      <van-cell is-link @click="isReport=true">反馈垃圾内容</van-cell>
      <van-cell>拉黑作者</van-cell>
    </van-cell-group>
    <van-cell-group v-else>
      <van-cell icon="arrow-left" @click="isReport=false">返回</van-cell>
      <van-cell v-for="item in reports" :key="item.value" @click="report(item.value)">{{item.label}}</van-cell>
    </van-cell-group>
  </van-popup>
</template>

<script>
import { disLikes, report } from '@/api/articles'
import { reports } from '@/api/constants'
export default {
  props: {
    // 控制对话框显示隐藏
    value: {
      type: Boolean,
      default: false
    },
    // 文章的id
    articleId: {
      type: String,
      default: null
    }
  },
  data () {
    return {
      isReport: false,
      reports
    }
  },
  methods: {
    // 不喜欢文章
    async dislikes () {
      try {
        await disLikes(this.articleId)
        // 隐藏对话框
        this.$emit('input', false)
        // 删除父组件对应id的文章
        this.$emit('on-dislikes')
        this.$toast.success('不感兴趣成功')
      } catch (error) {
        this.$toast.fail('不感兴趣失败')
      }
    },
    // 举报文章
    async report (type) {
      try {
        await report(this.articleId, type)
        this.$emit('input', false)
        this.$emit('on-report')
        this.$toast.success('举报成功')
      } catch (error) {
        // 409 此用户已举报过该文章（冲突）
        if (error.response && error.response.status === 409) {
          return this.$toast.fail('重复举报')
        }
        this.$toast.fail('举报失败')
      }
    }
  }
}
</script>

<style lang="less" scoped>
.van-popup {
  width: 80%;
  border-radius: 4px;
}
</style>
